<template>
  <div class="hs-container">
    <hs-header :title="$t('base.Refund')" isBackNav></hs-header>

    <hs-scroll class="tableView" :data="[details]">
      <ul>
        <train-segment :detail="details.Route">
          <hs-cell-1 :showLogo="false" @click.native="_getRefundDetails">
            <div class="cell-segment-one">
              <p>{{details.Route.SeatTypeName}}</p>
              <p>{{$t('base.RefundInstructions')}}</p>
            </div>
          </hs-cell-1>
        </train-segment>

        <hs-section title="#">
          <div class="tips" slot="title">*具体价格以12306审核为准</div>
          <hs-cell-1 @click.native="_select(index)"
            v-for="(item, index) in details.Route.Tickets" :key="index"
            :showLogo="false">
            <div class="custom-cell">
              <div class="left">
                <p class="name">{{item.PsgName}}</p>
                <p>{{item.Credential.CredentialName}}&nbsp;{{item.Credential.CredentialNo}}</p>
              </div>
              <div class="right" v-if="details.Route.Tickets[index]">
                <p>{{$t('base.RefundFee')}}</p>
                <p class="price">¥{{item.RefundPrice}}</p>
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box.png')" v-show="details.Route.Tickets[index].select"></div>
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box-default.png')" v-show="!details.Route.Tickets[index].select"></div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

      </ul>
    </hs-scroll>

    <div class="footer">
      <hs-gradient-button type="auto" width="60%" height="40px" :title="$t('base.Refund')" @click="_gotoRefund"></hs-gradient-button>
    </div>

    <!-- 退票规则 -->
    <hs-dialog v-model="showRule">
      <div v-html="ruleDetails"></div>
    </hs-dialog>

  </div>
</template>
<script type='text/ecmascript-6'>
import axios from 'axios'
import eventBus from 'common/js/eventBus.js'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import TrainSegment from 'pages/train/share/train-segment'
import {kpiTrainSubmitRefund} from 'api'
export default {
  computed: {
    OrderID() {
      return this.$route.query.OrderID
    },
    details() {
      return JSON.parse(this.$route.query.details)
    }
  },
  data() {
    return {
      showRule: false,
      ruleDetails: ''
    }
  },
  methods: {
    /** 获取退票规则 */
    _getRefundDetails() {
      axios.get('/static/html/trainRefund.html').then((res) => {
        this.ruleDetails = res.data
        this.showRule = true
      })
    },

    /** 选择 */
    _select(index) {
      this.details.Route.Tickets[index].select = !this.details.Route.Tickets[index].select
      this.$forceUpdate()
    },

    /** 退票 */
    _gotoRefund() {
      let dataList = this.details.Route.Tickets.filter(v => v.select).map(v => v.TicketID)
      if (dataList.length === 0) {
        this.$toast(this.$t('TM.PleaseSelectThePassengerWhoNeedsToRefund'))
        return
      }
      let params = {
        TicketIds: dataList
      }
      kpiTrainSubmitRefund(params).then((res) => {
        if (res.ResultData.IsSuccess === true) {
          this.$toast(this.$t('TM.RefundSucceed'))
          eventBus.$emit(`refund`)
          this.$router.replace({path: '/train/orderDetail', query: {Id: this.OrderID, isOwner: true}})
        } else {
          this.$toast(this.$t('TM.RefundFailed'))
        }
      })
    }
  },
  components: {
    HsCell1,
    HsDialog,
    HsGradientButton,
    HsHeader,
    HsScroll,
    HsSection,
    TrainSegment
  }
}
</script>
<style lang='stylus' scoped='' rel='stylesheet/stylus'>
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.tableView
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    .cell-segment-one
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        font-size 0.8rem
        color $color_black_text_1

    // 提示
    .tips
      @extend .flex-row
      @extend .flex-start
      width 100%
      color $color_theme

    .custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 15px 0px
      >.left
        width calc(100% - 150px)
        >.name
          font-size 1rem
          font-weight bold
          color $color_black_text_0
          margin-bottom 10px
        >p
          font-size 0.8rem
          color $color_black_text_1
      >.right
        @extend .flex-row
        @extend .flex-end
        width 150px
        >p
          font-size 0.8rem
          color $color_black_text_1
        >.price
          color $color_theme
          margin 0 15px
        >.checkbox
          width 20px
          height 20px
          background-size 100% 100%

  >.footer
    @extend .flex-center
    width 100%
    height 50px
    border-top 1px solid $color_gray_layer
</style>



